.row{
    margin-top: 10px;
    margin-left: 20px;
}
::-webkit-scrollbar{
    display: none;
}
.movies-row{
    display: flex;
    overflow-x: scroll;
    margin-top: 20px;
    
}
.movie{
    /* width: 100%; */
    max-width: 280px;
    max-height: 200px;
    margin-right: 20px;
    transition: transform 400ms;
    

}

.movie:hover{
    transform: scale(1.2);
    
}